@import url(https://fonts.googleapis.com/css?family=Quicksand);
.App{
    padding-top: 40px;
    background: url(/Exams/static/media/fog-foggy-forest-4827.6a4c4eef.jpg) no-repeat center center fixed;
    background-size: cover;
    display: table;
    width: 100%;
    height: 100vh;
    position: relative;
    font-family: 'Quicksand', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.title{
    text-align: center;
    text-transform: uppercase;
}

input{
    margin: 5px;
}
input:focus{
    border: 3px solid #555;
}
.list-item{
    display: inline-block;
}
.btn-delete{
    float: right;
    padding: 2px 5px;
    margin-left: 2px;
    border-radius: 5px;
    background: rgb(255, 54, 104);
    cursor: pointer;
}
